.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 15vh;
    box-shadow: 0px 0px 10px white;
    background-color: #333333;
}

.navbar h1 {
    padding-left: 20px;
    font-size: 40px;
    font-weight: 600;
    color: #0ef;
    cursor: pointer;
}

nav {
    display: flex;
    gap: 20px;
    font-size: 20px;
    font-weight: 600;
    padding-right: 130px;
}

nav a {
    color: #fff;
    cursor: pointer;
}

nav a:hover {
    color: #0ef;
}

nav {
    padding-right: 90px;
}

nav a {
    padding-right: 40px;
    position: relative;
    font-size: 1.1rem;
    font-weight: 600;
    color: #fff;
    transition: .5s;
    padding: 6px 10px;
}

nav a span {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    border-bottom: 2px solid #0ef;
    border-radius: 15px;
    transform: scale(0) translateY(50px);
    opacity: 0;
    transition: .5s;
}

nav a:hover span {
    transform: scale(1) translateY(0);
    opacity: 1;
}
/* Add this to your existing CSS */
nav a.active {
    color: #0ef;
}

nav a.active span {
    transform: scale(1) translateY(0);
    opacity: 1;
}


/* =============MEDIA QUERIES========== */

@media screen and (max-width: 900px) {
    .links {
        display: none;
    }
}
